import React from 'react';
import {
    Switch,
    Route,
    Link,
    useRouteMatch
} from "react-router-dom";
import Tictactoe from './Tictactoe.js';
import RPS from './Rockpaperscissor.js';

export default function Games() {
    let match = useRouteMatch();

    return (
        <div>
            <h2>Games</h2>
            <ul>
                <li>
                    <Link to={`${match.url}/tictactoe`}>Tic Tac Toe</Link>
                </li>
                <li>
                    <Link to={`${match.url}/rockpaperscissor`}>Rock Paper Scissor</Link>
                </li>
            </ul>

            <Switch>
                <Route path={`${match.path}/tictactoe`}>
                    <Tictactoe />
                </Route>
                <Route path={`${match.path}/rockpaperscissor`}>
                    <RPS />
                </Route>
                <Route path={match.path}>
                    <h3>What would you like to play?</h3>
                </Route>
            </Switch>
        </div>
    )
}